.phones {
  
  &__list {
    padding: 0 40px;
    margin-bottom: 80px;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(auto-fill, 500px);
    grid-column-gap: 40px;
    grid-row-gap: 40px;
    justify-content: center;
  }

  &__item {
    width: 500px;
  }
}